<template>
    <div>
        <div class="container userArticle">
            <div class="row">
            <nav class="col navbar navbar-dark mb-3">
                <h2 class=" m-auto">Administration du site d'escrime d'Aubagne</h2>

                <NavDefault></NavDefault>
            </nav>
            </div>
            <div class="row justify-content-center">
                <div class="col-12 col-md-10 col-lg-8" id="allArticles">
                   
                    <div v-for="article in userArticles" :key="article.id" class="card bg-light my-3">
                        <div class="card-header bg-light d-flex align-items-center justify-content-between m-0 p-1">
                            <div>
                                <img :src="article.avatar" height="40" class="m-0 rounded-circle"/>
                                <span class="small text-dark m-0 p-1" >
                                    Posté par {{article.userName}} 
                                    <span v-if="!article.isActive" class="small text-danger">(supprimé)</span>, 
                                    le {{article.createdAt.slice(0,10).split('-').reverse().join('/') + ' à ' + article.createdAt.slice(11,16)}}
                                </span>
                            </div>                                
                            <div>
                                <a :href="'#/article/edit/' + article.id"><img src="/images/edit.svg" class="m-1 p-0" alt="Editer l'article'" title="Editer l'article"/></a>
                                <a :href="'#/article/drop/' + article.id"><img src="/images/drop.svg" class="m-1 p-0" alt="Supprimer l'article'" title="Supprimer l'article'"/></a>
                            </div>                               
                        </div>
                        <div class="card-body text-dark text-left">
              <h4 v-if="article.articleTitle !== ''">
                {{ article.articleTitle }}
              </h4>
              <p v-if="article.articleChapo !== ''">
                <b>{{ article.articleChapo }}</b>
              </p>
              <p class="small" v-if="article.article !== ''">
                {{ article.article }}
              </p>
              <img
                class="w-100"
                :src="article.articleUrl"
                alt="image de l'article"
                v-if="article.articleUrl !== ''"
              />
            </div>
                    </div>
                     <div class="row my-2">
                        <div class="col-6 pr-1"><a href="#/articles" class="btn btn-sm btn-block btn-success">...retour aux articles</a></div>
                        <div class="col-6 pl-1"><a href="#/compte" class="btn btn-sm btn-block btn-primary">retour à mon compte...</a></div>
                    </div>
                    <noUserArticle v-if="noUserArticle"></noUserArticle>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import noUserArticle from "./NoUserArticle"

import NavDefault from "./NavDefault"
import axios from "axios"
import Swal from "sweetalert2"

export default {
    name: "Articles User",
    components: { 
        noUserArticle,
         
        NavDefault
    },
    computed:{
        isLogged() {
            if (localStorage.getItem("token") !== null) {   
                return true
            }
            return false
        }
    },
    data() {
        return {
            noUserArticle: false,
            newImage: "",
            currentUserId: "", 
            newArticle: "",
            file: null,
            userArticles: []
        }
    },
    created: function() {
        if (localStorage.getItem("refresh")===null) {
            localStorage.setItem("refresh", 0)
            location.reload()
        }
        axios.get("http://127.0.0.1:3000/api/articles/users/" + localStorage.getItem("userId") ,{ headers: {"Authorization": "Bearer " + localStorage.getItem("token")} })
        .then(res => {
            const rep = res.data.ListeArticles
            if (rep.length === 0) { this.noUserArticle = true } else { this.noUserArticle = false }
            this.userArticles = rep
        })
        .catch((error)=>{
            const codeError = error.message.split("code ")[1]
            let messageError = ""
            switch (codeError){
                case "400": messageError = "La liste des articles n'a pas été récupérer !"; break
            }
            Swal.fire({
                title: "Une erreur est survenue",
                text: messageError || error.message,
                icon: "error",
                timer: 3500,
                showConfirmButton: false,
                timerProgressBar: true
            }) 
        })
    }
}
</script>

<style lang="scss">
 
.userArticle{
   padding-top:15em;
}

</style>